import React, { useEffect } from 'react'
import Reveal from 'reveal.js';

const Becerra = () => {

    useEffect(() => {
        let deck = new Reveal({
            margin: 0.1,
            width: '100%',
            height: '100%',
            controls: true,
            progress: false,
            loop: false,
            minScale: 0.2,
            maxScale: 1.5,
            transition: 'slide',
            blackgroundTransition: 'slide',
        })
        deck.initialize();
        console.log('initialized fool')
    }, [])

    return (
        <div className="reveal">
            <div className="slides">
                <section data-background-image='https://micarrerauniversitaria.com/wp-content/uploads/2018/03/medicina-del-trabajo-1.jpg'>
                    <p style={{ color: 'chartreuse' }}>
                        MEDICINA DEL TRABAJO
                    </p>
                    <p>
                        Esta normativa, establece la obligación de contar con un Servicio de Medicina y de Higiene y Seguridad Laboral.
                    </p>
                    <p>
                        La Ley 24557 de Riesgos del Trabajo tiene entre sus aspectos destacados
                    </p>
                    <ol>
                        <li>
                            Reducir la siniestralidad laboral a través de la prevención de los riesgos derivados del trabajo.
                        </li>
                        <li>
                            Reparar los daños derivados de accidentes de trabajo y enfermedades profesionales, incluyendo la rehabilitación del trabajador damnificado.
                        </li>
                        <li>
                            Promover la recalificación y recolocación de los trabajadores damnificados.
                        </li>
                    </ol>
                </section>
                <section data-background-image='https://micarrerauniversitaria.com/wp-content/uploads/2018/03/medicina-del-trabajo-1.jpg'>
                    <p style={{ color: 'chartreuse' }}>
                        HORAS PROFESIONALES
                    </p>
                    <p>
                        Que es procedente redefinir la cantidad de horas-profesional dedicado a estos servicios necesarios por trabajador según las características del establecimiento, y las tareas de los trabajadores, desarrollando el concepto de "trabajador equivalente".
                    </p>
                    <p style={{ color: 'chartreuse' }}>
                        SRT DETERMINA EL TIPO DE EXÁMENES
                    </p>
                    <p>
                        Que es conducente establecer que la SUPERINTENDENCIA DE RIESGOS DEL TRABAJO sea la entidad responsable de determinar cuáles serán los exámenes médicos que deberán efectuar los empleadores o las
                        Aseguradoras, de acuerdo a lo establecido por el Decreto Nº 170/96.
                    </p>
                </section>
                <section data-background-image='https://micarrerauniversitaria.com/wp-content/uploads/2018/03/medicina-del-trabajo-1.jpg'>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            REGISTRO DE ACCIDENTES Y ENFERMEDADES
                        </p>
                        <p>
                            (Artículo 3º)
                            <br />Servicios de Medicina y de Higiene y Seguridad en el Trabajo.
                            <br />A los efectos del cumplimiento del artículo 5º apartado a) de la Ley N' 19.587, los establecimientos deberán contar, con carácter interno o externo según la voluntad del empleador, con Servicios de Medicina del Trabajo y de Higiene y Seguridad en el Trabajo, los que tendrán como objetivo fundamental:
                            <br />Prevenir, en sus respectivas áreas, todo daño que pudiera causarse a la vida y a la salud de los trabajadores, por las condiciones de su trabajo, creando las condiciones para que la salud v la seguridad sean una responsabilidad del conjunto de la organización.
                        </p>
                    </section>
                    <section>
                        <p>
                            (Artículo 4º)
                            <br />Trabajadores equivalentes. A los fines de la aplicación del presente se define como "cantidad de trabajadores equivalentes" a la cantidad que resulte de sumar el número de trabajadores dedicados a las tareas de producción más el CINCUENTA POR CIENTO (50%) del número de trabajadores asignados a tareas administrativas
                        </p>
                        <p>
                            <br />(Artículo 5º)
                            <br />Servicio de Medicina del Trabajo.
                            <br />El Servicio de Medicina del Trabajo tiene como misión fundamental:
                            <br />Promover y mantener el más alto nivel de salud de los trabajadores, debiendo ejecutar, entre otras, acciones de educación sanitaria, socorro, vacunación y estudios de ausentismo por morbilidad.

                        </p>
                    </section>
                </section>
                <section data-background-image='https://micarrerauniversitaria.com/wp-content/uploads/2018/03/medicina-del-trabajo-1.jpg'>
                    <p style={{ color: 'chartreuse' }}>
                        FUNCIÓN PRIORITARIAMENTE PREVENTIVA
                    </p>
                    <p>
                        Su función es esencialmente de carácter preventivo, sin perjuicio de la prestación de la asistencia inicial de las enfermedades presentadas durante el trabajo y de las emergencias médicas ocurridas en el establecimiento, hasta tanto se encuentre en condiciones de hacerse cargo el servicio médico que corresponda.
                    </p>
                </section>
                <section data-background-image='https://micarrerauniversitaria.com/wp-content/uploads/2018/03/medicina-del-trabajo-1.jpg'>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            ESPECIALIDAD
                        </p>
                        <p>
                            (Artículo 6º)
                            <br />Los Servicios de Medicina del Trabajo deberán estar dirigidos por graduados universitarios especializados en Medicina del Trabajo con título de Médico del Trabajo.
                            <br />(Artículo 7°)
                            <br />Los empleadores deberán disponer de la siguiente asignación de horas médico semanales en el establecimiento, en función del número de trabajadores equivalentes:
                            <br />Cantidad trabajadores equivalentes Horas-médico semanales
                            <br />A partir de MIL QUINIENTOS UN (1501) trabajadores equivalentes se deberá agregar, a las VEINTICINCO (25) horas previstas en el cuadro anterior. UNA (1) hora-médico semanal por cada CIEN (100) trabajadores.
                        </p>
                    </section>
                </section>
                <section data-background-image='https://micarrerauniversitaria.com/wp-content/uploads/2018/03/medicina-del-trabajo-1.jpg'>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            ENFERMERO
                        </p>
                        <p>
                            (Artículo 8º)
                            <br />Además de lo establecido en el artículo precedente, los empleadores deberán prever la asignación de personal auxiliar de estos Servicios de Medicina del Trabajo, consistente en un enfermero/a con título habilitante reconocido por la autoridad competente cuando existan en planta más de DOSCIENTOS
                            <br />ESPECIALIDAD
                            <br />(Artículo 9º)
                            <br />La SUPERINTENDENCIA DE RIESGOS DEL TRABAJO determinará los exámenes médicos que deberán realizar las Aseguradoras o los empleadores, en su caso, estipulando además, en función del riesgo a que se encuentre expuesto el trabajador al desarrollar su actividad, las características específicas y frecuencia de dichos exámenes.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            ROLES. ACTIVIDADES
                        </p>
                        <p>
                            Sin perjuicio de la reglamentación, el sistema de Higiene y Seguridad, prevé un rol activo para el servicio médico, con determinadas actividades, que se habrán de enunciar a modo ilustrativo para el estudiante.
                        </p>
                        <p>
                            Esto implica la toma de conocimiento de todas las funciones que el servicio médico debe asumir para complementar con el responsable en higiene y seguridad.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            FUNCIONES BÁSICAS
                        </p>
                        <ol>
                            <li>
                                Atención a la salud de los trabajadores
                            </li>
                            <li>
                                Prevención de los riesgos laborales para la salud de los trabajadores
                            </li>
                            <li>
                                Organización de los primeros auxilios
                            </li>
                            <li>
                                Atención médica de primer contacto
                            </li>
                            <li>
                                Control de la atención médica a los trabajadores
                            </li>
                            <li>
                                Rehabilitación para el trabajo
                            </li>
                            <li>
                                Compensación de los daños a la salud de los trabajadores
                            </li>
                            <li>
                                Organización de la respuesta médica a emergencias
                            </li>
                            <li>
                                Educación en Medicina del Trabajo y capacitación en Salud en el Trabajo
                            </li>
                            <li>
                                Investigación en Medicina del Trabajo
                            </li>
                            <li>
                                Administración de servicios de salud
                            </li>
                        </ol>
                    </section>
                </section>
                <section data-background-image='https://micarrerauniversitaria.com/wp-content/uploads/2018/03/medicina-del-trabajo-1.jpg'>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            ELEMENTOS DE DESEMPEÑO (COMPETENCIAS)
                        </p>
                        <p style={{ color: 'chartreuse' }}>
                            Evaluación de la salud de los trabajadores, a nivel individual y colectivo.
                        </p>
                        <p>
                            Analiza los efectos del modo de producción y de la composición orgánica del capital, sobre la salud de los trabajadores.
                            <br /> Explica la influencia de la organización del trabajo sobre la salud de los trabajadores. Valora los factores extra laborales que influyen en la salud de los trabajadores.
                            <br /> Diseña sistemas de información y bases de datos sobre salud de los trabajadores.
                            <br /> Describe y analiza el proceso salud – enfermedad de los trabajadores en términos estadísticos. Analiza las características del proceso salud–enfermedad de los trabajadores y de los factores asociados, en relación con las variables de tiempo, lugar y población.
                            <br /> Formula el diagnóstico de salud de la población trabajadora. Aplica pruebas ergométricas y procedimientos antropométricos para el estudio de los trabajadores.
                            <br /> Estima la salud y la aptitud físicas de los trabajadores. Valora la salud mental de los trabajadores, en función de las condiciones y del ambiente de trabajo.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Promoción de la salud de los trabajadores.
                        </p>
                        <p>
                            Reconoce la influencia de la cultura de la población trabajadora, sobre el estilo de vida y el proceso salud–enfermedad. Analiza el proceso salud–enfermedad de los trabajadores, con base en los diversos modelos de causalidad.
                            <br /> Esquematiza los problemas prioritarios de salud de los trabajadores, a partir del modelo de historia natural de la enfermedad. Propone intervenciones para incidir en la salud de los trabajadores, con base en los niveles de prevención de Leavell y Clark. Formula planes y programas de acción dirigidos a estimular una cultura de salud en la población trabajadora, de acuerdo con las premisas y estrategias de la promoción de la salud (marketing de la salud; dinámicas de grupos; elaboración de materiales,)
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Conservación y mejoramiento de la salud y aptitud de los trabajadores.
                        </p>
                        <p>
                            Integra grupos de trabajadores, con objetivos de salud específicos. Propone intervenciones para el uso del tiempo libre, a fin de mejorar el estilo de vida de los trabajadores.
                            <br /> Promueve el establecimiento de la pausa para la salud en la jornada laboral. Determina el gradiente de salud de los trabajadores, individual y colectiva.
                            <br /> Establece un plan de acción para conservar o mejorar la composición corporal y la capacidad de desempeño físico de los trabajadores.
                            <br /> Determina el patrón de alimentación y nutrición individual o colectiva, de los trabajadores. Participa en el análisis bromatológico y organoléptico de los alimentos (disponibles en la región; de la dieta tradicional; de productos recomendados).
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Evaluación y vigilancia de las exposiciones de los trabajadores derivadas de sus actividades laborales.
                        </p>
                        <p>
                            Aplica el método de evaluación de riesgos para la salud de los trabajadores, en el desempeño de su trabajo.
                            <br /> Identifica la presencia y magnitud de los agentes y factores de riesgo en los puestos de trabajo. Estima la nocividad potencial de las exposiciones de los trabajadores a los agentes y factores de riesgo, en relación con sus efectos en la salud.
                            <br /> Elabora mapas de riego.
                            <br /> Determina las dosis de exposición de los trabajadores a los agentes físicos, químicos y biológicos. Establece la dinámica de riesgos en la génesis de los accidentes de trabajo.
                            <br /> Pondera los factores de riesgo trabajador/trabajo como causas potenciales o reales de accidentes de trabajo.
                            <br /> Reconoce las manifestaciones de estrés en la actuación de los trabajadores. Clasifica los puestos de trabajo de acuerdo a sus requerimientos de gasto energético.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Control de las exposiciones laborales de los trabajadores.
                        </p>
                        <p>
                            Propone intervenciones para el control de las exposiciones de los trabajadores, aplicables ha: la fuente, el agente, el ambiente general, el propio trabajador.
                            <br /> Promueve acciones específicas para el control de los factores de riesgo del hombre, del trabajo y del ambiente, relacionados con los accidentes de trabajo.
                            <br /> Sugiere medidas generales de control y solución a los factores de riesgo ergonómicos relacionados con los accidentes de trabajo y el disconfort.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Integración de las brigadas de primeros auxilios.
                        </p>
                        <p>
                            Aplica las etapas del proceso administrativo para la conformación, organización y control de brigadas de primeros auxilios.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Dotación y vigilancia del uso de botiquines de primeros auxilios.
                        </p>
                        <p>
                            Formula programas para la integración y distribución de botiquines para los primeros auxilios, en relación con el diagnóstico de salud del centro de trabajo. Designa responsables de los botiquines. Mantiene actualizado el inventario de insumos de los botiquines de primeros auxilios y evalúa la eficiencia y efectividad de su uso, en relación con la morbilidad
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Diagnóstico; atención o prescripción iniciales para estabilizar o contener la evolución de lesiones o padecimientos de los trabajadores.
                        </p>
                        <p style={{ fontSize: '32px' }}>
                            Reconoce las manifestaciones iniciales de los problemas de salud de los trabajadores, de inicio agudo o de evolución crónica, derivados de sus exposiciones laborales o de índole general, determina plan de manejo terapéutico inmediato y efectúa la referencia correspondiente para su atención médica subsecuente, en materia de:
                        </p>
                        <ul style={{ fontSize: '32px' }}>
                            <li>
                                Audiología laboral
                            </li>
                            <li>
                                Dermatología del Trabajo Neumología del Trabajo
                            </li>
                            <li>
                                Neurología del Trabajo
                            </li>
                            <li>
                                Oftalmología del Trabajo
                            </li>
                            <li>
                                Otorrinolaringología laboral
                            </li>
                            <li>
                                Psiquiatría y psicología del trabajo
                            </li>
                            <li>
                                Toxicología del trabajo Alergia e inmunología Cardiología
                            </li>
                            <li>
                                Hematología
                            </li>
                            <li>
                                Endocrinología
                            </li>
                            <li>
                                Gastroenterología
                            </li>
                            <li>
                                Angiología
                            </li>
                        </ul>
                    </section>
                    <section>
                        <ul style={{ fontSize: '32px' }}>
                            <li>
                                Urología
                            </li>
                            <li>
                                Traumatología y ortopedia
                            </li>
                            <li>
                                Biología de la reproducción
                            </li>
                            <li>
                                Microbiología y parasitología
                            </li>
                            <li>
                                Reanimación cardiopulmonar
                            </li>
                            <li>
                                Soporte vital en traumatismos graves
                            </li>
                            <li>
                                TRIAGE
                            </li>
                        </ul>
                        <p>
                            Aplica las disposiciones legales relativas a la atención inicial, estabilización, clasificación, referencia y traslado de trabajadores lesionados o enfermos.
                            <br /> Establece el procedimiento para la referencia y traslado de trabajadores, para su atención en unidades médicas oficiales o privadas
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Manejo y control de un cuadro básico de medicamentos, de acuerdo con el diagnóstico de salud y con el marco normativo aplicable.
                        </p>
                        <p>
                            Diseña un cuadro básico de medicamentos, en función del diagnóstico de salud del centro laboral, de las acciones terapéuticas deseadas, de las características de conservación y administración de los medicamentos y de la compatibilidad o limitaciones con la actividad laboral; realiza la prescripción de medicamentos de acuerdo con los principios científicos, éticos y normativos aplicables. Establece un programa para la dotación de insumos, la actualización de existencias, el control de inventarios la evaluación de la eficiencia y efectividad en el manejo del cuadro básico. Identifica las disposiciones legales y normativas aplicables a la integración, mantenimiento y control de un cuadro básico de medicamentos
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Integración y registro actualizado del expediente médico legal de cada trabajador, de acuerdo con el marco normativo aplicable.
                        </p>
                        <p>
                            Aplica las disposiciones legales y normativas sobre la integración de expedientes, el registro de datos sobre las exposiciones y los eventos de salud de los trabajadores; así mismo, sobre la confidencialidad y el derecho a la información.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Evaluación y vigilancia de la atención médica que se otorga a los trabajadores, orientada a la resolución de los daños y la contención de costos.
                        </p>
                        <p>
                            Desarrolla los modelos de Historia Natural de la enfermedad correspondientes a los problemas prioritarios de salud de los trabajadores, relativos a la patología del trabajo y patología general. Elabora estudios de trazadores y protocolos de la atención médica que se brinda a los trabajadores, correspondientes a sus problemas de salud prioritarios.
                            <br /> Establece un sistema para evaluar la efectividad y calidad de los proveedores de servicios médicos, institucionales o particulares, con indicadores, criterios e instrumentos.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Gestión de la atención médica para los trabajadores, ante instituciones públicas y privadas.
                        </p>
                        <p>
                            Evalúa los indicadores de la atención médica que se proporciona a los trabajadores, derivada de patología del trabajo y patología general.
                            <br /> Vigila la consistencia del comportamiento de los proveedores de servicios médicos, de acuerdo con indicadores y criterios establecidos.
                            <br /> Conduce los procedimientos para el seguimiento y resolución de los casos que igualan o rebasan los valores de referencia de los indicadores de la atención médica.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Selección de proveedores de servicios de atención médica, con base en criterios de calidad, capacidad resolutiva, eficiencia y eficacia
                        </p>
                        <p>
                            Establece un sistema de (indicadores, estándares, criterios, instrumentos y procedimientos) para la evaluación y selección de proveedores de servicios médicos.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Promoción de la reincorporación oportuna al trabajo de la reubicación temporal y de la readaptación.
                        </p>
                        <p>
                            Evalúa la capacidad funcional residual de los trabajadores con limitaciones derivadas de accidentes o enfermedades. Determina el pronóstico laboral del trabajador con pérdidas anatómicas y/o limitaciones funcionales.
                            <br /> Aplica los principios y procedimientos de la readaptación, la reeducación y el reacomodo laboral. Promueve la rehabilitación en el centro de trabajo.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Valoración médico–legal de los casos de riesgos de trabajo, de invalidez y de incapacidad temporal prolongada.
                        </p>
                        <p>
                            Analiza la evolución de las lesiones o enfermedades de los trabajadores, correspondientes a patología general o de trabajo, de acuerdo con el modelo de historia natural.
                            <br /> Identifica inconsistencias e insuficiencias de la atención médica que se proporciona a los trabajadores, en relación con los protocolos de atención y los términos de ley aplicables. Determina los diagnósticos nosológicos, etiológico y anatomofuncional, así como el pronóstico laboral de los trabajadores enfermos, lesionados o con secuelas, con base en evidencias clínicas, de laboratorio, de gabinete y estudios de campo.

                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Análisis médico–legal de dictámenes y peritajes por riesgos de trabajo o de invalidez.
                        </p>
                        <p>
                            Valora el expediente clínico, los reportes de laboratorio, los estudios de imagenología y de campo utilizados para determinar la situación de salud y el pronóstico laboral de los trabajadores con secuelas de lesiones o enfermedades derivadas de patología laboral o general. Juzga la pertinencia y procedencia de las disposiciones legales aplicadas al otorgamiento de prestaciones e indemnizaciones por riesgos de trabajo; o bien Sustenta juicios médico-técnico-legales para interponer recursos administrativos o controversias en casos concretos de patología laboral o general, ante instituciones y autoridades jurisdiccionales.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Participación de las brigadas de primeros auxilios, de rescate, de evacuación de lesionados y de información, para actuar en citaciones de emergencia.
                        </p>
                        <p>
                            Elabora planes y programas para la formación de brigadas y la capacitación de los trabajadores integrantes, en función del análisis de riesgos e la empresa.
                            <br /> Aplica métodos y técnicas didácticas para el adiestramiento y el entrenamiento periódico de los integrantes de la brigada de primeros auxilios, y colabora con las de evacuación de personas, rescate de lesionados y de información en situaciones de emergencia.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Capacitación específica a la brigada de primeros auxilios y vigilancia de la aplicación de las técnicas de atención pre hospitalaria.
                        </p>
                        <p>
                            Aplica métodos y técnicas didácticas para el adiestramiento y el entrenamiento periódico de los integrantes de la brigada de primeros auxilios, sobre atención pre hospitalaria.
                            <br /> Promueve la certificación de los integrantes de la brigada de primeros auxilios en RCP y PHTLS.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'chartreuse' }}>
                            Establecimiento de criterios, coordinación y procedimientos ante situaciones de emergencia y para la atención pre hospitalaria.
                        </p>
                        <p>
                            Establece la coordinación con unidades de servicios médicos públicas y privadas, para la atención de lesionados, en situaciones emergentes.
                            <br /> Diseña los procedimientos para el traslado y la admisión en unidades médicas, de los lesionados en situaciones emergentes. Conduce simulacros para evaluar la efectividad del sistema. Establece criterios de pronóstico clínico, vital y funcional, para clasificar y determinar la prioridad de atención a lesionados en situaciones emergentes.
                        </p>
                    </section>
                </section>
            </div>
        </div>
    );
}

export default Becerra;
