import React, { useEffect } from 'react'
import Reveal from 'reveal.js';

const Mili = () => {

    useEffect(() => {
        let deck = new Reveal({
            margin: 0.1,
            width: '100%',
            height: '100%',
            controls: true,
            progress: false,
            loop: false,
            minScale: 0.2,
            maxScale: 1.5,
            transition: 'slide',
            blackgroundTransition: 'slide',
        })
        deck.initialize();
        console.log('initialized fool')
    }, [])

    return (
        <div className="reveal">
            <div className="slides" data-transition="slide">
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <section>
                        <p>
                            <span style={{ color: 'lightgreen' }}> INTRODUCCIÓN </span>

                            <br></br> El contrato de trabajo importa un cúmulo de efectos, interpartes y hacia la sociedad.
                            <br></br> Existen normas que conforman el llamado orden Público Laboral, que determinan una plexo normativo obligatorio de cumplimiento efectivo, e irrenunciable.
                            <br></br> El trabajo produce efectos económicos sociales.
                            <br></br> Sabemos que el empleo es uno de los presupuestos que más preocupan en la actualidad
                            <br></br> Esta protección alcanza la cuestión remuneratoria, los formatos del contrato, pero fundamentalmente lo que se busca proteger es lo atinente a la SALUD DEL TRABAJADOR.

                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> ¿Quién es el trabajador?  </span>

                            <br></br> Es el sujeto que se inserta en una organización, es por ello que la ley pone en la cabeza del empleador todo el deber de seguridad , higiene y ergonomía que debe acompañar a la organización del ambiente de trabajo, a la selección de las herramientas y maquinarias, a la entrega de los elementos de protección individuales y colectivos, a la capacitación en obrar seguro, buenas conductas, buenas prácticas de manufactura, todo lo que debe ser organizado en cumplimiento estricto de dos normas fundamentales como resultan:
                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> 1. LA LEY DE HIGIENE Y SEGURIDAD, 2. LA LEY DE RIESGOS DEL TRABAJO </span>

                            <br></br> En la República Argentina, todos los empleadores quedan alcanzados por las pautas de la ley 19587 de Higiene y seguridad, esta ley es completa, con niveles de técnica que responden igualmente a los requerimientos internacionales. Siendo la fuente normativa que marca los niveles de higiene y seguridad que deben regir en cada actividad. Es de cumplimiento OBLIGATORIO.
                        </p>
                    </section>
                </section>
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <section>
                        <p>
                            <span style={{ color: 'lightgreen' }}> COMPONENTES DE LA LEY </span>

                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> ÁMBITO DE APLICACIÓN: </span>

                            <br></br> En el art 1 de la ley se indica que “Las condiciones de higiene y seguridad en el trabajo se ajustarán, en todo el territorio de la República, a las normas de la presente ley y de las reglamentaciones que en su consecuencia se dicten”.
                            <br></br> LA LEY DE HIGIENE Y SEGURIDAD TIENE JURISDICCIÓN EN TODA LA NACIÓN
                            <br></br> EN QUÉ ESTABLECIMIENTO SE APLICARÁ? : Sus disposiciones se aplicarán a todos los establecimientos y explotaciones.
                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> DEFINICIÓN DE ESTABLECIMIENTO: </span>

                            <br></br> Como lo dispone el segundo artículo de la ley, entiende que : “ …………………………los términos "establecimiento", "explotación", "centro de trabajo" o "puesto de trabajo" designan todo lugar destinado a la realización o donde se realicen tareas de cualquier índole o naturaleza con la presencia permanente, circunstancial, transitoria o eventual de personas físicas y a los depósitos y dependencias anexas de todo tipo en que las mismas deban permanecer o a los que asistan o concurran por el hecho o en ocasión del trabajo o con el consentimiento expreso o tácito del principal.
                            <br></br> <span style={{ color: 'bisque' }}> Aspectos a destacar: </span>
                            <ul>
                                <li>no hay límite  para el tipo de tareas</li>
                                <li>no influye la extensión de tiempo del trabajo, estable o temporario</li>
                                <li>enunciados que repite la ley de riesgos “por el hecho o en ocasión del trabajo”</li>
                            </ul>
                        </p>
                    </section>
                </section>
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <section>
                        <p>
                            <span style={{ color: 'lightgreen' }}> DEFINE EL CONCEPTO DE EMPLEADOR </span>

                            <br></br> El término empleador designa a la persona que utiliza la actividad de una o más personas en virtud de un contrato o relación de trabajo.

                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> OBJETO DE LA LEY DE HIGIENE Y SEGURIDAD </span>

                            <br></br> (Art. 4º) Comprenderá las normas técnicas y medidas sanitarias, precautorias, de tutela o de cualquier otra índole que tengan por objeto:
                            <br></br> Primer principios
                            <ul>
                                <li>Proteger la vida, preservar y mantener la integridad psicofísica de los trabajadores; reducir riesgos.</li>
                                <li>Prevenir, reducir, eliminar los riesgos de los distintos centros o puestos de trabajo.</li>
                            </ul>

                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> ACTUAR PROACTIVAMENTE </span>
                            <ul>
                                <li>Estimular y desarrollar una actitud positiva respecto de la prevención de los accidentes o enfermedades que puedan derivarse de la actividad laboral.</li>
                            </ul>
                        </p>
                    </section>
                </section>
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <section>
                        <p>
                            <span style={{ color: 'lightgreen' }}> PRINCIPIOS Y MÉTODOS DE EJECUCIÓN </span>

                            <br></br> (Art. 5º) A los fines de la aplicación de esta ley considérense como básicos los siguientes principios y métodos de ejecución:

                            <br></br> PRINCIPIO EJECUTOR, CREA LOS PROFESIONALES ESPECIALIZADOS
                            <ul>
                                <li>Creación de servicios de higiene y seguridad en el trabajo; REGLAMENTOS Y PROGRAMAS DE PREVENCIÓN EN SEGURIDAD</li>
                            </ul>
                        </p>
                    </section>
                    <section>
                        <p>
                            <ul>
                                <li>Institucionalización gradual de un sistema de reglamentaciones, atendiendo a condiciones ambientales o factores ecológicos y a la incidencia de las áreas o factores de riesgo; FORMACIÓN DE PROGRAMAS DE SEGURIDAD SEGÚN AREAS</li>
                                <li>Sectorialización de los reglamentos en función de ramas de actividad ; CLASIFICA LAS ACTIVIDADES SEGÚN EL RIESGO</li>
                                <li>Distinción a todos los efectos de esta ley entre actividades normales, riesgosas o de vejez o agotamiento prematuros y/o las desarrolladas en lugares o ambientes insalubres; LA NORMALIZACIÓN SE DEBE REALIZAR CON LAS NORMAS EXISTENTES COMO IRAM , UNAE, OSHAS</li>
                                <li>Normalización de los términos utilizados en higiene y seguridad, estableciéndose definiciones concretas y uniformes para la clasificación de los accidentes, lesiones y enfermedades del trabajo; PROMOCIÓN DE LA INVESTIGACIÓN PREVENTIVA PARA LA DETECCIÓN DE LOS RIESGOS</li>
                            </ul>
                        </p>
                    </section>
                    <section>
                        <p>
                            <ul>
                                <li>investigación de los factores  de los accidentes y enfermedades del trabajo, especialmente de los físicos, fisiológicos y sicológicos; PROMOCIÓN DE LA ESTADÍSTICA, CON EL ANÁLISIS DE LOS ACCIDENTES Y CAUSAS IGUAL CON LAS ENFERMEDADES</li>
                                <li>realización y centralización de estadísticas normalizadas sobre accidentes y enfermedades del trabajo como antecedentes para el estudio de las causas determinantes y los modos de prevención; ESPECIAL DEDICACIÓN EN TODAS LAS ACTIVICADES PENOSAS</li>
                                <li>estudio y adopción de medidas para proteger la salud y la vida del trabajador en el ámbito de sus ocupaciones, especialmente en lo que atañe a los servicios prestados en tareas riesgosas  y/o las desarrolladas en lugares o ambientes insalubres; CON LA INFLUENCIA DE ERGONOMÍA IMPONE REALIZAR TÉCNICAS DE CORRECCIÓN SUPUESTOS DE RIESGOS PERMANENTES</li>
                                <li>aplicación de técnicas de corrección de los ambientes de trabajo en los casos en que los niveles de los elementos agresores, nocivos para la salud, sean permanentes durante la jornada de labor; DEFINIR LOS MODELOS DE APTITUD PARA UNA CORRECTA SELECCIÓN DE PERSONAL</li>
                            </ul>
                        </p>
                    </section>
                    <section>
                        <p>
                            <ul>
                                <li>fijación de principios orientadores en materia de selección e ingreso de personal en función de los riesgos a que den lugar las respectivas tareas, operaciones y manualidades profesionales; ESTO SURGE CON LOS SISTEMAS INTEGRADO DE GESTION DE SALUD, SEGURIDAD Y MEDIO AMBIENTE</li>
                                <li>determinación de condiciones mínimas de higiene y seguridad para autorizar el funcionamiento de las empresas o establecimientos; EL EMPLEADOR DEBE CAPACITARSE AL IGUAL QUE SUS EMPLEADOS INTERACTUANDO CON EL PODER PÚBLICO</li>
                                <li>adopción y aplicación, por intermedio de la autoridad competente; EL EMPLEADOR DEBE SER DILIGENTE EN SI Y PARA SUS EMPLEADOS EN CUANTO A LOS PROGRAMAS DE CAPACITACIÓN</li>
                                <li>participación en todos los programas de higiene y seguridad de las instituciones especializadas, públicas y privadas; LA LEY DE HS DEFIENDE LA VIGENCIA DE LAS NORMAS INTERNACIONALSES DE SEGURIDAD ( IRAM, ISO, UNAE, OSHAS)</li>
                            </ul>
                        </p>
                    </section>
                    <section>
                        <p>
                            <ul>
                                <li>observancia de las recomendaciones internacionales en cuanto se adapten a las características propias del país, en las condiciones previstas precedentemente, de los convenios internacionales en la materia; DEBER DE INFORMACIÓN A SU ENTORNO Y EMPLEADOS</li>
                                <li>difusión y publicidad de las recomendaciones y técnicas de prevención que resulten universalmente aconsejables o adecuadas; DEBER DE REALIZAR LOS EXAMENES MÉDICOS</li>
                                <li>realización de exámenes médicos pre-ocupacionales y periódicos, de acuerdo a las normas que se establezcan en las respectivas reglamentaciones.</li>
                            </ul>
                        </p>
                    </section>
                </section>
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <p>
                        <span style={{ color: 'lightgreen' }}> REGLAMENTOS DE HIGIENE QUE SE DEBEN RESPETAR: </span>
                        
                        <ul>
                            <li>Las reglamentaciones de las condiciones de higiene de los ambientes de trabajo deberán considerar:</li>
                            <ul>
                                <li>características de diseño de plantas industriales, establecimientos, locales, centros y puestos de trabajo, maquinarias, equipos y procedimientos seguidos en el trabajo;</li>
                                <li>factores físicos: cubaje, ventilación, temperatura, carga térmica, presión, humedad, iluminación, ruidos, vibraciones y radiaciones ionizantes;</li>
                                <li>contaminación ambiental: agentes físicos y/o químicos y biológicos;</li>
                                <li>efluentes industriales.</li>
                            </ul>
                        </ul>
                    </p>
                </section>
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <section>
                        <p>
                            <span style={{ color: 'lightgreen' }}> CONDICIONES DE SEGURIDAD </span>
                            

                            <br></br> Las reglamentaciones de las condiciones de seguridad en el trabajo deberán considerar:
                            <ul>
                                <li>instalaciones, artefactos y accesorios; útiles y herramientas: ubicación y conservación;</li>
                                <li>protección de máquinas, instalaciones y artefactos;</li>
                                <li>instalaciones eléctricas;</li>
                                <li>equipos de protección individual de los trabajadores;</li>
                                <li>prevención de accidentes del trabajo y enfermedades del trabajo;</li>
                                <li>identificación y rotulado de sustancias nocivas y señalamiento de lugares peligrosos y singularmente peligrosos;</li>
                                <li>prevención y protección contra incendios y cualquier clase de siniestros.</li>
                            </ul>
                        </p>
                    </section>
                </section>
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <section>
                        <p>
                            <span style={{ color: 'lightgreen' }}> ES OBLIGACIÓN DEL EMPLEADOR ADOPTAR MEDIDAS DE HIGIENE Y SEGURIDAD ADECUADAS </span>
                            

                            <br></br> (art.8°)Todo empleador debe adoptar y poner en práctica las medidas adecuadas de higiene y seguridad para proteger la vida y la integridad de los trabajadores, especialmente:

                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> ALCANCE </span>
                            
                            <ul>
                                <li>a la construcción, instalación y equipamiento de los edificios y lugares de trabajo en condiciones ambientales y sanitarias adecuadas;</li>
                                <li>a la colocación y mantenimiento de resguardos y protectores de maquinarias y de todo género de instalaciones, con los dispositivos de higiene y seguridad que la mejor técnica aconseje;</li>
                                <li>al suministro y mantenimiento de los equipos de protección personal;</li>
                                <li>a las operaciones y procesos de trabajo.</li>
                            </ul>
                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> OTRAS OBLIGACIONES DEL EMPLEADOR EN EL MARCO DE LA LEY </span>
                            
                            <br></br> Son  obligaciones del empleador;
                            <ul>
                                <li>disponer el examen pre-ocupacional y revisación periódica del personal, registrando sus resultados en el respectivo legajo de salud;</li>
                                <li>mantener en buen estado de conservación, utilización y funcionamiento, las maquinarias, instalaciones y útiles de trabajo;</li>
                                <li>instalar los equipos necesarios para la renovación del aire y eliminación de gases, vapores y demás impurezas producidas en el curso del trabajo;</li>
                                <li>mantener en buen estado de conservación, uso y funcionamiento las instalaciones eléctricas y servicios de aguas potables;
                                    evitar la acumulación de desechos y residuos que constituyan un riesgo para la salud, efectuando la limpieza y desinfecciones periódicas pertinentes;</li>
                            </ul>
                        </p>
                    </section>
                    <section>
                        <p>
                            <ul>
                                <li>eliminar, aislar o reducir los ruidos y/o vibraciones perjudiciales para la salud de los trabajadores;</li>
                                <li>instalar los equipos necesarios para afrontar los riesgos en caso de incendio o cualquier otro siniestro;</li>
                                <li>depositar con el resguardo consiguiente y en condiciones de seguridad las sustancias peligrosas;</li>
                                <li>disponer de medios adecuados para la inmediata prestación de primeros auxilios;</li>
                                <li>colocar y mantener en lugares visibles avisos o carteles que indiquen medidas de higiene y seguridad o adviertan peligrosidad en las maquinarias e instalaciones;</li>
                                <li>promover la capacitación del personal en materia de higiene y seguridad en el trabajo, particularmente en lo relativo a la prevención de los riesgos específicos de las tareas asignadas;</li>
                                <li>denunciar accidentes y enfermedades del trabajo.</li>
                            </ul>
                        </p>
                    </section>
                </section>
                <section data-background-image='https://cdn2.hubspot.net/hubfs/2302063/Article-accidentes.png'>
                    <section>
                        <p>
                            <span style={{ color: 'lightgreen' }}> DEBERES DE LOS TRABAJADORES </span>
                            

                            <br></br> (Art. 10) El trabajador estará obligados a:
                            <ul>
                                <li>
                                    cumplir con las normas de higiene y seguridad y  las obligaciones de uso, conservación y cuidado del equipo de protección personal y de los propios de las maquinarias, operaciones y procesos de trabajo;
                                </li>
                            </ul>
                        </p>
                    </section>
                    <section>
                        <p>
                            <span style={{ color: 'bisque' }}> CUMPLIR CON LOS EXAMENES MÉDICOS </span>
                            

                            <ul>
                                <li>someterse a los exámenes médicos preventivos o periódicos y cumplir con las prescripciones e indicaciones que a tal efecto se le formulen; CUIDADO DE LOS ELEMENTOS</li>
                                <li>cuidar los avisos y carteles que indiquen medidas de higiene y seguridad y observar sus prescripciones; DEBER DE COLABORACIÓN COMPROMISO, RESPONSABILIDAD</li>
                                <li>colaborar en la organización de programas de formación y educación en materia de higiene y seguridad y asistir a los cursos que se dictaren durante las horas de labor.</li>
                            </ul>
                        </p>
                    </section>
                </section>
            </div>
        </div>
    );
}

export default Mili;
