import React, { useEffect } from 'react'
import Reveal from 'reveal.js';

const Romano = () => {

    useEffect(() => {
        let deck = new Reveal({
            margin: 0.1,
            width: '100%',
            height: '100%',
            controls: true,
            progress: false,
            loop: false,
            minScale: 0.2,
            maxScale: 1.5,
            transition: 'slide',
            blackgroundTransition: 'slide',
        })
        deck.initialize();
        console.log('initialized fool')
    }, [])

    return (
        <div className="reveal">
            <div className="slides">
                <section data-background-image='https://blog.eclass.com/hubfs/shutterstock_526166827-1.png'>
                    <p style={{ color: 'sandybrown' }}>
                        ¿Qué es el Comité Paritario?
                    </p>
                    <p>
                        Es el organismo técnico y fundamental en la prevención de riesgos dentro de la empresa.
                        Debe actuar con la administración superior de la empresa, con la supervisión de línea, con el departamento de prevención de riesgos y otras unidades de asesorías de la empresa, con el organismo administrador y con los propios trabajadores.
                    </p>
                </section>
                <section data-background-image='https://blog.eclass.com/hubfs/shutterstock_526166827-1.png'>
                    <p style={{ color: 'sandybrown' }}>
                        ¿Quiénes integran el Comité Paritario?
                    </p>
                    <p>
                        Los Comités Paritarios son obligatorios en empresas con más de 25 trabajadores. Está compuesto por seis integrantes, tres representantes de la empresa y tres representantes de los trabajadores.
                        Por cada miembro titular deberá existir otro en carácter de suplente, los que entrarán a reemplazar a los titulares en caso de impedimento de éstos por cualquier causa o vacancia del cargo.
                    </p>
                </section>
                <section data-background-image='https://blog.eclass.com/hubfs/shutterstock_526166827-1.png'>
                    <p style={{ color: 'sandybrown' }}>
                        ¿Cómo se forma un Comité Paritario?
                    </p>
                    <ul style={{ fontSize: '32px' }}>
                        <li>Con la designación de los representantes de la empresa por la administración superior.</li>
                        <li>Con la elección de los representantes de los trabajadores mediante votación secreta y directa.</li>
                        <li>El comité designará entre sus miembros un presidente y un secretario</li>
                        <li>Los requisitos básicos que deben cumplir los representantes de la empresa son:</li>
                        <ol>
                            <li>Persona vinculada a las actividades técnicas que se desarrollan en la industria, sucursal o agencia</li>
                            <li>Aunque no es exigible, es recomendable que haya participado en un curso de orientación en prevención de riesgos.</li>
                        </ol>
                        <li>Los requisitos básicos que deben cumplir los representantes de los trabajadores:</li>
                        <ol>
                            <li>Tener más de 18 años de edad.</li>
                            <li>Saber leer y escribir.</li>
                            <li>Antigüedad en la empresa no inferior a un año, excepto en casos calificados por la ley.</li>
                            <li>Acreditar haber asistido a un curso de orientación en prevención de riesgos, o haber prestado servicios en esta área por un año como mínimo.</li>
                        </ol>
                    </ul>
                </section>
                <section data-background-image='https://blog.eclass.com/hubfs/shutterstock_526166827-1.png'>
                    <p style={{ color: 'sandybrown' }}>
                        ¿Cuáles son las funciones del Comité Paritario?
                    </p>
                    <ul style={{ fontSize: '32px' }}>
                        <li>
                            Asesorar e instruir a los trabajadores para la correcta utilización de los instrumentos de protección.
                        </li>
                        <li>
                            Vigilar el cumplimiento tanto por parte de la empresa como de los trabajadores, de las medidas de prevención, higiene y seguridad.
                        </li>
                        <li>
                            Investigar las causas de los accidentes del trabajo y enfermedades profesionales que se produzcan en la empresa.
                        </li>
                        <li>
                            Decidir si el accidente o la enfermedad profesional se debió a negligencia inexcusable del trabajador o no.
                        </li>
                        <li>
                            Indicar la adopción de todas las medidas de higiene y seguridad que sirvan para la prevención de los riesgos profesionales.
                        </li>
                        <li>
                            Cumplir las demás funciones o misiones que le encomiende el organismo administrador respectivo.
                        </li>
                        <li>
                            Promover la realización de cursos de capacitación profesional para los trabajadores
                        </li>
                        <li>
                            Informar por decisión de la empresa, al trabajador recién ingresado acerca de los riesgos que entrañan sus labores, de las medidas preventivas y de los métodos de trabajo correctos.
                        </li>
                    </ul>
                </section>
                <section data-background-image='https://blog.eclass.com/hubfs/shutterstock_526166827-1.png'>
                    <section>
                        <p style={{ color: 'sandybrown' }}>
                            ¿Cómo realizan estos sus actividades?
                        </p>
                        <p>
                            De las reuniones:
                            El comité deberá reunirse en forma ordinaria una vez al mes; pero podrán hacerlo en forma extraordinaria a petición conjunta de un representante de la empresa y uno de los trabajadores.<br />
                            En forma obligada el comité, deberá reunirse, cuando ocurra un accidente del trabajo que cause la muerte de uno o más trabajadores o a juicio del presidente pudiera producirse una incapacidad de ganancia superior al 40%.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'sandybrown' }}>
                            Las reuniones serán dirigidos por el presidente y podrán tratarse materias como:

                        </p>
                        <ul>
                            <li>
                                Revisión de los acuerdos tomados en la reunión anterior.
                            </li>
                            <li>
                                Control y análisis del programa de trabajo.
                            </li>
                            <li>
                                Análisis de los accidentes ocurridos en el mes.
                            </li>
                            <li>
                                Presentación de los problemas detectados en las inspecciones de seguridad, además de aquellos comunicados por los trabajadores o los miembros del comité.
                            </li>
                            <li>
                                Estudio de campañas de prevención o su desarrollo.
                            </li>
                            <li>
                                Otros temas de interés relacionados con la prevención de riesgos.
                            </li>
                        </ul>
                    </section>
                    <section>
                        <p>
                            Además deberá dejarse constancia en acto de lo tratado y un extracto de ello, puesto en conocimiento de la dirección de la empresa, los trabajadores y el organismo administrador.<br />
                            Las reuniones, se efectuarán en horas de trabajo, considerándose como trabajado el tiempo en ellos empleado. Por decisión de la empresa, las reuniones podrán efectuarse fuera del horario de trabajo; pero en tal caso, el tiempo ocupado en ellos será considerado como' trabajo extraordinario para efecto de su remuneración'.
                        </p>
                    </section>
                    <section>
                        <p>
                            Del programa de actividades:
                        </p>
                        <p>
                            Con el propósito de cumplir con sus funciones el comité debe trabajar basado en un programa definido, cuyas actividades podrán estar repartidas entre los miembros del comité, organizados en comisiones de trabajo; los que deberán estar integrados por miembros de cada representación, para que puedan actuar como comité.
                        </p>
                        <p>
                            Las comisiones realizarán su trabajo mediante visitas a terreno, jerarquizando los problemas encontrados, de acuerdo con su importancia y magnitud, solicitando asesoría técnica si lo requieren para estudiar y determinar soluciones, fijando prioridades de las acciones a seguir.
                        </p>
                    </section>
                </section>
            </div>
        </div>
    );
}

export default Romano;
