import React, { useEffect } from 'react'
import Reveal from 'reveal.js';

const Mariscal = () => {

    useEffect(() => {
        let deck = new Reveal({
            margin: 0.1,
            width: '100%',
            height: '100%',
            controls: true,
            progress: false,
            loop: false,
            minScale: 0.2,
            maxScale: 1.5,
            transition: 'slide',
            blackgroundTransition: 'slide',
        })
        deck.initialize();
        console.log('initialized fool')
    }, [])

    return (
        <div className="reveal">
            <div className="slides" data-transition="slide">
                <section data-transition="slide" data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <h2 className='r-fit-text' style={{ color: 'aquamarine' }}>INTRODUCCIÓN AL MARCO REGULATORIO</h2>
                    <p className='r-fit-text'>REPARACIÓN, PREVENCIÓN Y TRATAMIENTO DE LOS INFORTUNIOS LABORALES EN LA EMPRESA </p>
                    <p className='r-fit-text'>Ley de Riesgos del trabajo 24557</p>
                </section>
                <section data-transition="slide" data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <section data-transition="slide">
                        <p style={{ color: 'aquamarine' }}>Antecedentes:</p>
                        <p>La ley 24.557 fue sancionada por el Congreso de la Nación en septiembre del año 1995 y promulgada por el Poder Ejecutivo en Octubre del mismo año. Dicha ley reemplazó el régimen estatuido por la ley 24.028, imperante hasta esa época, la cual rigió desde diciembre del 1991 hasta la entrada de la ley 24.557, la cual derrogó a la ley 9688 que establecía el antiguo sistema de responsabilidad por accidentes del trabajo, que regía desde 1915 hasta la vigencia de la ley 24.028.</p>
                    </section>
                    <section data-transition="slide">
                        <p>La ley 24.557 plantea reducir la siniestralidad laboral con la prevención, reparar los daños causados por los accidentes de trabajo y enfermedades profesionales, rehabilitar, recalificar y recolocar al trabajador accidentado, y promover la negociación colectiva laboral para mejorar las medidas de prevención y de las prestaciones reparadoras.</p>
                    </section>
                    <section data-transition="slide">
                        <p>Las principales diferencias entre el antiguo régimen y el que introdujo la norma en análisis, es la imposición respecto del empleador de un seguro obligatorio, esta vez con una entidad de objeto único la cual la postre sería la única responsable directa frente al damnificado del resarcimiento, como así también de las prestaciones consagradas por la ley, es decir, se trata de un seguro propio y especifico de la ley 24.557, que tiene la particularidad de consagrar como entidad aseguradora a las Administradoras de Riesgos del Trabajo ART.</p>
                    </section>
                </section>
                <section data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <section>
                        <p style={{ color: 'aquamarine' }}>NUEVAS FIGURAS</p>
                        <p style={{ fontSize: '32px' }}>Aparecen nuevas figuras en este ámbito, quienes asumen la función de ser las únicas deudoras de las prestaciones dinerarias y estéticas al damnificado, con la excepción en que el empleador haya decidido auto asegurarse o no afiliarse a una ART, circunstancia que generan el desplazamiento del sistema de responsabilidad estatuido. Se basa en la gestión descentralizada del seguro obligatorio, que deben contratar todos los empleadores, tanto del sector privado como público, con excepción de aquellas empresas y/o empleadores que han decidido y podido, de acuerdo a la ley, auto asegurarse. La señalada gestión descentralizada está en manos de las Aseguradoras de Riesgos de Trabajo. De esta manera el empleador queda al margen de toda responsabilidad por accidentes del trabajo del que fueran víctimas sus dependientes, salvo el caso de dolo consagrado en el art. 1072 del Código Civil y el auto aseguramiento antes señalado. Así el empleador no era el deudor directo, ni solidario, por las prestaciones dinerarias, ni médicas, ni farmacéuticas, ni de rehabilitación, ni de ningún tipo, por lo que las ART son las responsables directas y únicas del otorgamiento y pago de las prestaciones en especie y dinerarias, que consagra el sistema de la ley 24.557.</p>
                    </section>
                    <section>
                        <p style={{ fontSize: '32px' }}>La gran diferencia entre el viejo y el nuevo régimen es que, el viejo, buscaba únicamente la reparación del infortunio laboral, mientras que el nuevo se centraba en la prevención como primera medida lo cual constituye una importante novedad y avance ya que la acción preventiva recobra importancia capital, a partir de las medidas de higiene, seguridad y medicina del trabajo al posible evento dañoso, neutralizándolo o minimizándolo logrando "reducir la siniestralidad laboral a través de la prevención de los riesgos derivados del trabajo" y el sistema que entonces estaba vigente había mostrado su incapacidad para "reducir la frecuencia y gravedad de los siniestros" (Antecedentes parlamentarios, Buenos Aires, La Ley, 1996-A, ps. 408, 409 y 411). El trámite legislativo en el seno del Congreso Nacional, de su lado, no hizo más que subrayar el objetivo de la prevención, que resultaba "lo sustancial" del proyecto, el "objetivo primordial" y "primario" de éste, según lo puso de resalto el miembro informante del dictamen de mayoría en el Senado (ídem, p. 546), entre otras intervenciones de diputados y senadores (ídem, ps. 458, 483, 567 y pássim). (Fallo CSJN Nº: T. 205. XLIV “Recurso de hecho deducido por La Caja ART S.A. en la causa Torillo, Atilio Amadeo y otro c/ Gulf Oil Argentina S.A. y otro”).</p>
                    </section>
                </section>
                <section data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <p style={{ color: 'aquamarine' }}>CONDICIONES DIGNAS DE TRABAJO</p>
                    <p style={{ fontSize: '32px' }}>La prevención en la protección de la salud y de la integridad física del trabajador es el presupuesto legítimo de la prestación de servicios, que no puede ya concebirse sin la adecuada preservación de la dignidad inherente a la persona humana. La protección de la integridad psicofísica del trabajador, cuando no de la vida misma de éste, mediante la prevención en materia de riesgos laborales resulta, sin dudas, una cuestión en la que alcanza su mayor significación y gravedad la doctrina de esta Corte, según la cual, aquél es un sujeto de preferente tutela constitucional. Podemos decir entonces, que las principales modificaciones introducidas respecto del anterior régimen son: exclusión del empleador como sujeto pasivo y responsable directo del evento dañoso, creación del sistema de aseguramiento obligatorio, general y único a través de las Aseguradoras de Riesgos del Trabajo (ART) o por autoseguro, creación de un lisado cerrado y taxativo de enfermedades profesionales e indemnizables, prestaciones dinerarias mensualizadas, a la par de la posibilidad de percepción de una suma adicional de pago único, prestaciones en especie de diversa naturaleza, sustanciación de los conflictos que provoque la aplicación e interpretación de la citada ley, por fuera del ámbito del poder judicial, a través del establecimiento de Comisiones Médicas.</p>
                </section>
                <section data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <p style={{ color: 'aquamarine' }}>2.- Objetivos de la Ley 24.557.</p>
                    <p style={{ fontSize: '32px' }}>
                        Esta ley, en su art. 1 sostiene: “Son objetivos de la Ley sobre Riesgos del Trabajo (LRT):
                    </p>
                    <ul style={{ fontSize: '32px' }}>
                        <li>Reducir la siniestralidad laboral a través de la prevención de los riesgos derivados del trabajo;</li>
                        <li>Reparar los daños derivados de accidentes de trabajo y de enfermedades profesionales, incluyendo la rehabilitación del trabajador damnificado;</li>
                        <li>Promover la recalificación y la recolocación de los trabajadores damnificados;</li>
                        <li>Promover la negociación colectiva laboral para la mejora de las medidas de prevención y de las prestaciones reparadoras”.</li>
                        <li>Para lograr estos objetivos, el legislador puso a cada uno de los integrantes del sistema, una serie de deberes de cuyo cumplimiento depende de la concreción de aquellos. La persecución de dichos objetivos tiene la finalidad de permitir recrear condiciones de trabajo equitativas y satisfactorias.</li>
                    </ul>
                </section>
                <section data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <p style={{ color: 'aquamarine' }}>3.- Vigencia temporal de la ley 24.557:</p>
                    <p>
                        Si bien la ley fue sancionada el 13 de septiembre de 1995, promulgada el día 3 de octubre del mismo año, y publicada en el Boletín Oficial de la Nación el 4 de octubre de 1995, el legislador no previó una fecha general y única de inicio de vigencia de la misma. Ello, toda vez que a tenor de su art. 49, dejó supeditada la entrada en vigencia de dicha ley a que el comité consultivo permanente creado por el art. 40 de la misma ley, apruebe por consenso el listado de enfermedades profesionales y la tabla de evaluación de incapacidades, la cual debería realizarse dentro del plazo de 180 días a contar desde la promulgación de la ley.
                    </p>
                </section>
                <section data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <section>
                        <p style={{ color: 'aquamarine' }}>4.- Estructura ley 24.557</p>
                        <p>
                            En cuanto a la estructura elegida por el legislador, debemos decir que la ley 24557 muestra una singular estructura, producto de la interacción de distintos factores tales como la novedad del sistema que implementa, la compleja articulación de sus disposiciones con las particularidades del mercado asegurador, la preexistencia de diferentes siniestros algunos de los cuales susceptible de tratamiento bajo la vigencia de los regímenes legales anteriores. Si tuviéramos que transcribir la estructura literal de la ley 24557 pergeñada por el legislador, deberíamos decir que a diferencias de otras normas, esta ha distribuido todo su contenido en el ámbito de quince capítulos, sin incorporar por el contrario, título alguno.
                        </p>
                    </section>
                    <section>
                        <p style={{ color: 'aquamarine' }}> Así las cosas tenemos que el: </p>
                        <ul style={{ fontSize: '32px' }}>
                            <li>CAPÍTULO I, objetivos y ámbito de aplicación de la ley.</li>
                            <li>CAPÍTULO II, prevención de los riesgos del trabajo</li>
                            <li>CAPÍTULO III, contingencias y situaciones cubiertas</li>
                            <li>CAPITULO IV, prestaciones dinerarias</li>
                            <li>CAPÍTULO V, prestaciones en especie</li>
                            <li>CAPÍTULO VI, determinación y revisión de las incapacidades</li>
                            <li>CAPITULO VII, régimen financiero</li>
                            <li>CAPITULO VIII, gestión de las prestaciones</li>
                            <li>CAPITULO IX, derechos, deberes y prohibiciones</li>
                            <li>CAPITULO X, fondo de la garantía de la LRT</li>
                            <li>CAPITULO XI, fondo de reserva de la LRT</li>
                            <li>CAPITULO XII, entes de regulación y supervisión de la LRT</li>
                            <li>CAPITULO XIII, responsabilidad civil del empleador</li>
                            <li>CAPITULO XIV, órgano tripartito de participación</li>
                            <li>CAPITULO XV, normas generales y complementarias</li>
                        </ul>
                    </section>
                </section>
                <section data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <section>
                        <p style={{ color: 'aquamarine' }}> 5.- Contingencias: </p>
                        <p>
                            <br></br> En lo que concierne a las contingencias cubiertas por el nuevo régimen, podemos decir que a tenor del artículo 6° de la ley 24.557, las contingencias de cobertura obligatoria, son:
                        </p>
                        <ol>
                            <li>El accidente de trabajo</li>
                            <li>la enfermedad profesional</li>
                            <li>el accidente in itinere</li>
                            <li>la incapacidad e invalidez</li>
                            <li>la muerte del trabajador</li>
                            <li>el desempleo como contingencia derivada de la invalidez parcial y permanente</li>
                        </ol>
                    </section>
                    <section>
                        <p>
                            Concepto Accidente de Trabajo: se denomina accidente de trabajo al hecho súbito, generalmente violento y traumático que se produce dentro del ámbito laboral o por el hecho o en ocasión del trabajo y que lesiona física o sicológicamente al trabajador y le produce una incapacidad.
                            “Art. 6 inciso 1. Se considera accidente de trabajo a todo acontecimiento súbito y violento ocurrido por el hecho o en ocasión del trabajo”.
                        </p>
                    </section>
                    <section>
                        <p style={{ fontSize: '32px' }}>
                            Concepto de Enfermedad Profesional: son las enfermedades originadas ante la presencia en el lugar de trabajo, de un agente hostil que produce incapacidad para trabajar y que generalmente tiene lenta evolución.
                            Vale recalcar que para la ley 24.557, sólo serán enfermedades profesionales aquellas incluidas en el listado respectivo, ya que la misma establece un sistema de lista cerrada, rígida y de triple columna.

                            <br></br> Art. 6 inciso 2 a) Se consideran enfermedades profesionales aquellas que se encuentran incluidas en el listado que elaborará y revisará el Poder Ejecutivo, conforme al procedimiento del artículo 40 apartado 3 de esta ley. El listado identificará agente de riesgo, cuadros clínicos, exposición y actividades en capacidad de determinar la enfermedad profesional. 
                            <br></br> 2 b) Serán igualmente consideradas enfermedades profesionales aquellas otras que, en cada caso concreto, la Comisión Médica Central determine como provocadas por causa directa e inmediata de la ejecución del trabajo, excluyendo la influencia de los factores atribuibles al trabajador o ajenos al trabajo.
                        </p>
                    </section>
                    <section>
                        <p style={{ fontSize: '32px' }}>
                            Concepto de Accidente In itinere: se entiende por tal, si el trabajador por alguna circunstancia modificara el recorrido que acostumbraba realizar para llegar desde su morada al lugar de trabajo y viceversa, y en el transcurso de dicho recorrido sufre un evento dañoso, no será considerado como tal, por lo que quedará exento de reparación en el marco de la ley 24.557.

                            <br></br> “Art. 6 inciso 1. Se considera accidente de trabajo a todo acontecimiento súbito y violento ocurrido por el hecho o en ocasión del trabajo, o en el trayecto entre el domicilio del trabajador y el lugar de trabajo, siempre y cuando el damnificado no hubiere interrumpido o alterado dicho trayecto por causas ajenas al trabajo. El trabajador podrá declarar por escrito ante el empleador, y éste dentro de las setenta y dos (72) horas ante el asegurador, que el itinere se modifica por razones de estudio, concurrencia a otro empleo o atención de familiar directo enfermo y no conviviente, debiendo presentar el pertinente certificado a requerimiento del empleador dentro de los tres (3) días hábiles de requerido”.
                        </p>
                    </section>
                    <section>
                        <p style={{ fontSize: '32px' }}>
                            Concepto de incapacidad e invalidez: es el estado en que se encuentra la persona que no puede desarrollar normalmente su trabajo, por efecto de un accidente o una enfermedad profesional. El estado de incapacidad puede ser temporario o permanente, total o parcial.

                            <br></br> “Artículo 7° — Incapacidad Laboral Temporaria.

                            <br></br> 1. Existe situación de Incapacidad Laboral Temporaria (ILT) cuando el daño sufrido por el trabajador le impida temporariamente la realización de sus tareas habituales.

                            <br></br> Artículo 8° — Incapacidad Laboral Permanente.

                            <br></br> 1. Existe situación de Incapacidad Laboral Permanente (ILP) cuando el daño sufrido por el trabajador le ocasione una disminución permanente de su capacidad laborativa”.

                            <br></br> Artículo 10. — Gran invalidez.

                            <br></br> Existe situación de gran invalidez cuando el trabajador en situación de Incapacidad Laboral Permanente total necesite la asistencia continua de otra persona para realizar los actos elementales de su vida.
                        </p>
                    </section>
                    <section>
                        <p>
                            Concepto de muerte como contingencia social: la muerte del trabajador es una contingencia social por cuanto afecta los ingresos de su grupo familiar que generalmente vive merced a los ingresos generados por aquel. Por tal motivo, su muerte puede producir desamparos y otros efectos negativos.

                            <br></br> Concepto de desempleo como contingencia: el trabajador afectado por un estado de invalidez tendrá graves dificultades para reinsertarse en el medio laboral, de modo que la ley de riesgos del trabajo procura potenciar las facultades del trabajador, capacitándolo para que logre conquistar nuevos puestos de trabajo a partir de sus nuevas posibilidades.
                        </p>
                    </section>
                </section>
                <section style={{ fontSize: '32px' }} data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <section>
                        <p style={{ color: 'aquamarine' }}> 6.- Prestaciones: </p>
                        <p>
                            Las contingencias más arriba descriptas, generan necesariamente una de serie de prestaciones, las cuales están también contempladas por la ley 24.557 de manera expresa y a cargo de las Administradoras de Riesgos del Trabajo, salvo los casos de excepción antes señalado. Al respecto, tenemos que señalar que aquel cuerpo normativo, contempla dos grandes tipos de prestaciones, ellas son: las en especie y las dinerarias.

                            <br></br> Siguiendo a Grisolía (Derecho del Trabajo y de la Seguridad Social, Tomo II), podemos decir que las prestaciones en especies son servicios y beneficios para asistir al trabajador, mientras dure el estado en que se encuentra a causa del accidente laboral o de la enfermedad profesional.
                            <br></br> Dentro de este grupo de prestaciones, tenemos la asistencia médica y farmacéutica, las prótesis y ortopedia, la rehabilitación, la recalificación profesional y el servicio funerario.
                            <br></br> Y aquí nos encontramos con otra de las diferencias y avances respecto del anterior régimen, toda vez que la ley 24.557 a las típicas prestaciones en especie (medico-farmacéuticas) agrega la rehabilitación y la recalificación del damnificado.
                        </p>
                    </section>
                    <section>
                        <p>
                            De la interpretación armónica de los art. 20, 26 y 30 de la ley 24.557, surge la obligación de las ART y de los empleadores autoasegurados (de otorgar en forma íntegra y oportuna las prestaciones en especie hasta la curación completa del trabajador o mientras subsistan los síntomas incapacitantes. (Ley del Riesgos del Trabajo. Comentada y anotada con jurisprudencia. Alvarez Chavez. Editorial García Alonso). Idénticas obligaciones recaen respecto de los empleadores no asegurados ni autosegurados. En este sentido, el ARTÍCULO 20 dispone:
                            <br></br> Las ART otorgaran a los trabajadores que sufran algunas de las contingencias previstas en esta ley las siguientes prestaciones en especie: 
                            <br></br> Asistencia médica y farmacéutica: Prótesis y ortopedia:
                            <br></br> Rehabilitación;
                            <br></br> Recalificación profesional; y Servicio funerario.
                            <br></br> Las ART podrán suspender las prestaciones dinerarias en caso de negativa injustificada del damnificado, determinada por las comisiones médicas, a percibir las prestaciones en especie de los incisos a), c) y d).
                            <br></br> Las prestaciones a que se hace referencia en el apartado 1, incisos a), b) y c) del presente artículo, se otorgaran a los damnificados hasta su curación completa o mientras subsistan los síntomas incapacitantes, de acuerdo a como lo determine la reglamentación.

                        </p>
                    </section>
                    <section>
                        <p>
                            Y el ARTÍCULO 30 indica: Auto seguro.
                            Quienes hubiesen optado por el régimen de auto seguro deberán cumplir con las obligaciones que esta ley pone a cargo del empleador y a cargo de las ART, con la excepción de la afiliación, el aporte al Fondo de Reserva de la LRT y toda otra obligación incompatible con dicho régimen.
                            <br></br>En este mismo sentido, el ARTICULO 28. — Responsabilidad por omisiones.
                            <br></br>1. Si el empleador no incluido en el régimen de autoseguro omitiera afiliarse a una ART, responderá directamente ante los beneficiarios por las prestaciones previstas en esta ley.
                            <br></br>Así las cosas, las prestaciones dinerarias, tienen como objetivo reemplazar aquellos salarios que el trabajador venía percibiendo al momento del evento dañoso en razón de que a causa de este precisamente, se encuentra imposibilitado de generarlos. De esta manera, dentro de las prestaciones dinerarias, tenemos los salarios dejados de percibir por el trabajador a causa del evento dañoso que contempla la ley, las indemnizaciones de pago periódico y aquellas de pago único.
                            <br></br>A efecto de clarificar un poco más este tema, cabe hacer un distingo entre incapacidad laboral temporaria e incapacidad laboral permanente, toda vez que dependiendo del tipo de incapacidad que sufra el trabajador, el pago de la prestación dineraria será justamente de pago mensual o único.
                            <br></br>Así las cosas, si el trabajador se encuentra accidentado o padece una enfermedad profesional que le genere una incapacidad laboral temporaria, recibirá como remuneración un monto de dinero equivalente al sueldo, que se denomina valor mensual del ingreso base.
                            <br></br>Durante los primeros diez días posteriores al accidente o enfermedad profesional, dicho valor mensual de ingreso base lo debe abonar el empleador, mientras que el resto de los días-para el caso de persistir aquel evento- corresponde a la ART. El pago en este caso se debe realizar en forma mensual.

                        </p>
                    </section>
                </section>
                <section style={{ fontSize: '32px' }} data-background-image='https://www.caracteristicas.co/wp-content/uploads/2018/08/derecho-laboral-e1533320358446.jpg'>
                    <section>
                        <p style={{ color: 'aquamarine' }}> 7.- Derechos y deberes de los sujetos integrantes del sistema. </p>
                        <p>
                            <br></br>Como señaláramos más arriba, campea en la ley de riesgos del trabajo la idea fuerza de la prevención y ello hace justamente que la misma adquiera identidad propia a la par que se diferencie de los regímenes que la antecedieron.
                            <br></br>De allí que y en pos de la idea de la prevención, el legislador haya puesto en cabeza de todos y cada uno de los integrantes del sistema –trabajador, empleador y ART-, el deber y obligación de adoptar todas aquellas medidas legalmente previstas para prevenir eficazmente los riesgos del trabajo.

                        </p>
                    </section>
                    <section>
                        <p>
                            La ley 24.557 al respecto consagra en su ARTÍCULO 4°: Obligaciones de las partes.
                            <br></br>Los empleadores y los trabajadores comprendidos en el ámbito de la LRT, así como las ART están obligados a adoptar las medidas legalmente previstas para prevenir eficazmente los riesgos del trabajo. A tal fin y sin perjuicio de otras actuaciones establecidas legalmente,    dichas partes    deberán asumir compromisos concretos de cumplir con las normas sobre higiene y seguridad en el trabajo. Estos compromisos podrán adoptarse en forma unilateral, formar parte de la negociación colectiva, o incluirse dentro del contrato entre la ART y el empleador.

                        </p>
                    </section>
                    <section>
                        <p>
                            Las Aseguradoras de Riesgos del Trabajo deberán establecer exclusivamente para cada una    de las empresas o establecimientos considerados críticos, de conformidad a lo que determine la autoridad de aplicación, un plan de acción que contemple el cumplimiento de las siguientes medidas:

                            <br></br>La evaluación periódica de los riesgos existentes y su evolución;
                            <br></br>Visitas periódicas de control de cumplimiento de las normas de prevención de riesgos del trabajo y del plan de acción elaborado en cumplimiento de este artículo;
                            <br></br>Definición de las medidas correctivas que deberán ejecutar las empresas para reducir los riesgos identificados y la siniestralidad registrada;
                            <br></br>Una propuesta de capacitación para el empleador y los trabajadores en materia de prevención de riesgos del trabajo.

                        </p>
                    </section>
                    <section>
                        <p>
                            Las ART y los empleadores estarán obligados a informar a la Superintendencia de Riesgos del Trabajo o a las Administraciones de Trabajo provinciales, según corresponda, la formulación y el desarrollo del plan de acción establecido en el presente artículo, conforme lo disponga la reglamentación.
                            <br></br>A los efectos de la determinación del concepto de empresa crítica, la autoridad de aplicación deberá considerar especialmente, entre otros parámetros, el grado de cumplimiento de la normativa de higiene y seguridad en el trabajo, así como el índice de siniestralidad de la empresa.
                            <br></br>La ART controlará la ejecución del plan de acción y estará obligada a denunciar los incumplimientos a la Superintendencia de Riesgos del Trabajo.
                            Las discrepancias acerca de la ejecución del plan de acción serán resueltas por la Superintendencia de Riesgos del Trabajo.

                        </p>
                    </section>
                </section>
            </div>
        </div>
    );
}

export default Mariscal;
